import RoundBox from "../styleComponents/RoundBox";
import { MUSIC_SVG } from '../assets/svg/addmusic.svg';
import { Formik, Form, ErrorMessage, Field, FieldArray } from "formik";
import * as Yup from 'yup';

const initialValues = {
    name: '',
    email: '',
    channel: '',
    phNumbers: ['']
}

const onSubmit = (values, props) => {
    console.log("Form Submitted : ", values);
    console.log("Form Submitted Props : ", props);

}

const validationSchema = Yup.object({
    name: Yup.string().required("*This field is required"),
    email: Yup.string().required("*This field is required").email("*Invalid Email"),
    channel: Yup.string().required("*This field is required")
});

const validateComments = value => {
    let error;
    if (!value) {
        error = "*Required";
    }
    return error;
}


